import React from "react";
import { InputGroup, FormControl } from "react-bootstrap";
import { booleanANDBinary, binaryToDezimal, maxBinary, dezimalToBinary, addLeadingZeroes } from "./utils";

const IPV4SolverComponent = (props) => {


    const { mehStuff: [elemA, elemB] } = props;

    const [octet1, octet2, octet3, octet4] = elemA.map((item, i) => booleanANDBinary(item.octet, elemB[i].octet))
    const [subOctet1, subOctet2, subOctet3, subOctet4] = elemB.map(item => item.octet)

    const bcDezis = elemB.map((item, i) => maxBinary(item.octet, elemA[i].octet))

    const subOctets = [subOctet1, subOctet2, subOctet3, subOctet4]
    subOctets.forEach((item,i) => {
        if ([...item].some(x => x === "0")) {
            bcDezis[i] = Math.pow(2, [...item].reduce((a,v) => v === "0" ? a + 1 : a ,0)) - 1
        }
    })

    const [bcDezi1, bcDezi2, bcDezi3, bcDezi4] = bcDezis

    return (
        <>
            <h3>{props.title}</h3>
            <InputGroup className="mb-3">
                <InputGroup.Text>Dezimal</InputGroup.Text>
                <FormControl type="number" min="0" max="255" value={binaryToDezimal(octet1)} disabled />
                <FormControl type="number" min="0" max="255" value={binaryToDezimal(octet2)} disabled />
                <FormControl type="number" min="0" max="255" value={binaryToDezimal(octet3)} disabled />
                <FormControl type="number" min="0" max="255" value={binaryToDezimal(octet4)} disabled />
            </InputGroup>
            <InputGroup className="mb-3">
                <InputGroup.Text>Binary</InputGroup.Text>
                <FormControl type="text" value={octet1} disabled />
                <FormControl type="text" value={octet2} disabled />
                <FormControl type="text" value={octet3} disabled />
                <FormControl type="text" value={octet4} disabled />
            </InputGroup>
            <h3>Broadcast Address</h3>
            <InputGroup className="mb-3">
                <InputGroup.Text>Dezimal</InputGroup.Text>
                <FormControl type="number" min="0" max="255" value={bcDezi1} disabled />
                <FormControl type="number" min="0" max="255" value={bcDezi2} disabled />
                <FormControl type="number" min="0" max="255" value={bcDezi3} disabled />
                <FormControl type="number" min="0" max="255" value={bcDezi4} disabled />
            </InputGroup>
            <InputGroup className="mb-3">
                <InputGroup.Text>Binary</InputGroup.Text>
                <FormControl type="text" value={addLeadingZeroes(dezimalToBinary(bcDezi1))} disabled />
                <FormControl type="text" value={addLeadingZeroes(dezimalToBinary(bcDezi2))} disabled />
                <FormControl type="text" value={addLeadingZeroes(dezimalToBinary(bcDezi3))} disabled />
                <FormControl type="text" value={addLeadingZeroes(dezimalToBinary(bcDezi4))} disabled />
            </InputGroup>
        </>
    )
}


export default IPV4SolverComponent;